import { LocaleService } from '@farris/ui-locale';

@Component({
    selector: 'farris-text',
    template: `
        <span #textInner
            [ngClass]="{'f-form-control-text':!isTextArea,'f-form-control-textarea':isTextArea,'f-component-text-auto-size': this.autoSize}"
            [title]="staticTitle" [style.text-align]="textAlign"
            [style.height]="!autoSize&&height>0?height+'px':null" [style.minHeight]="autoSize&&height>0?height+'px':null"
            [style.maxHeight]="autoSize&&maxHeight>0?maxHeight+'px':null">{{text && text.length > 0 ?  text : control}}</span>
    `,
    styles: [],
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => TextComponent),
        multi: true,
    }]
})
export class TextComponent implements OnInit, ControlValueAccessor {
    @HostBinding('class.f-component-text') cls = true;
    @ViewChild('textInner', { read: ElementRef }) textInnerEl: ElementRef;
    @Output() textChange = new EventEmitter<any>();
    /** æ¯å¦ä¸ºå¯ç è¾å¥ */
    @Input() isPassword = false;
    // æ¯å¦å¤è¡ææ¬
    @Input() isTextArea = false;
    // æ¯å¦å¯ç¨èªå¨å°ºå¯¸
    @Input() autoSize = false;
    // è®¾ç½®æå¤§é«åº¦
    @Input() maxHeight: number;
    // è®¾ç½®é«åº¦
    @Input() height = 0;
    // æ§ä»¶ç±»å
    @Input() type: string;
    // æ¯å¦æ¯æ¥æèå´
    @Input() dateRange = false;
    // éè¦è½¬åç±»åæ ¼å¼çè¡¨è¾¾å¼
    @Input() format: any;
    // å¤è¯­è¨
    @Input() lang: string;
    // è½¬æ¢ç±»åç¨å°çæ°æ®
    @Input() data: any;
    // ååä½ç¬¦å·
    @Input() thousand = ',';
    // å°æ°ç¹å¤ç¬¦å·
    @Input() decimal = '.';
    // è´§å¸ç¬¦å·åå¼å±ç°å½¢å¼
    @Input() expression = '%s%v';
    @Input() currentLanguage: string;
    // formControlæ¾ç¤ºçææ¬
    @Input() showTime: boolean;
    @Input() textField = 'name';
    @Input() valueField = 'value';
    @Input() returnFormat = ''; // ä»æå¡å¨ç«¯åå¼æ¯æ ¼å¼ååç

    // ææ¬æ¹å
    @Input() textAlign;
    // æ¥æèå´æ¨¡å¼ä¸inputçæ¾ç¤ºåå²ç¬¦å
    @Input() dateRangeDelimiter = '~';
    @Input() enumDelimiter = ',';
    private _tshowType = 0;
    @Input()
    set showType(value: number) {
        this._tshowType = value;
        // å¦ææ¯å¨
        if (value == 4) {
            this.dateRange = true;
        }
    }
    get showType(): number {
        return this._tshowType;
    }
    /**
     * å½éåç±»å
     */
    @Input() localizationType: string;
    control: any;
    // æ®éææ¬
    text: any;
    // è´§å¸ç¬¦å·ï¼é»è®¤æ¯RMB
    currencySymbol = 'ï¿¥';
    // åè¿å¶åé¢å¡«åçç¬¦å·
    @Input() filledSymbol = '0';
    // æ®éææ¬è¾å¥å¼
    @Input()
    set value(value: any) {
        this.text = this.formatValue(value);
        this.textChange.emit(this.text);
    }
    get value() {
        return this.text;
    }
    @HostListener('mouseenter', ['$event']) changeTitle(ev) {
        if (!this.isTextArea && !this.isPassword) {
            this.mouseEnterChangeTitle();
        }
    }
    // æ ç­¾
    staticTitle = "";
    // å¤çä¸­è±ææåµï¼ å ä¸ºå¤è¯­è¨ææ¶ä¸éè¿è¿ä¸ªå±æ§æ§å¶
    @Input() showTitle = false;
    //æ°å¼æ ¼å¼åæ°
    _option;
    @Input()
    set option(value) {
        this._option = value;
    }
    get option() {
        return this._option;
    }

    /** æ¥æå¤é */
    @Input() multiSelect = false;
    /** æ¥æå¤éåéç¬¦ */
    @Input() separator = ',';

    private localeService: any;

    beforeWriteValue: (val: any, options: any) => string = null;

    private controlChange = (val) => { };
    private controlTouch = (val) => { };
    private debugSer: DebugService = null;
    constructor(
        private dtHelper: DateTimeHelperService,
        private numberHelper: NumberHelperService,
        private injector: Injector,
        public ngzone: NgZone
    ) {
        this.localeService = this.injector.get(LocaleService);
        this.debugSer = this.injector.get(DebugService, null);
    }

    ngOnInit() {
    }
    ngOnDestroy(): void {
    }
    ngAfterViewInit(): void {
        if (this.isTextArea) {
            const clsName = this.textInnerEl.nativeElement.parentElement.className;
            this.textInnerEl.nativeElement.parentElement.className = clsName + ' f-cmp-text-is-textarea';
        }
    }
    private mouseEnterChangeTitle() {
        let textEl=this.textInnerEl.nativeElement;
        if (Math.abs(textEl.scrollWidth - textEl.clientWidth) > 2 || Math.abs(textEl.scrollHeight - textEl.clientHeight) > 2) {
            this.staticTitle = this.text && this.text.length > 0 ? this.text : this.control;
        } else {
            this.staticTitle = '';
        }
    }

    /**
     * å¤çå¤è¯­è¨ï¼å¾å®
     */
    formatLang(value: any) {
        return value && value[this.currentLanguage] ? value[this.currentLanguage] : '';
    }
    /**
     * Cè´§å¸
     * Dåè¿å¶
     * Fæµ®ç¹æ°
     * Næ°å­ï¼å¸¦ååä½
     * Pç¾åæ¯
     */
    getFormatNumberValue(value: any) {
        if (!this.format && !this._option) {
            return value.toString();
        }
        if (this._option && this._option.type && this._option.type == 'number') {
            if (this._option.formatter) {
                return this._option.formatter(value);
            } else {
                if (this._option.useThousands) {
                    if (this._option.precision != undefined) {
                        this.format = 'n' + this._option.precision;
                    } else {
                        this.format = 'n2';
                    }
                } else {
                    if (this._option.precision != undefined) {
                        this.format = 'f' + this._option.precision;
                    } else {
                        this.format = 'f2';
                    }
                }
            }
        }
        const firstLetter = this.currencyToUpperCase(this.format.substring(0, 1));
        const precision = Number(this.format.substring(1));
        let config = {};
        let result: any;
        if (!/C|D|F|N|P/g.test(firstLetter)) {
            if (this.debugSer) {
                this.debugSer.warn(`ä¸æ¯æformatä¸º ${this.format} ç±»åçæ°å­æ ¼å¼å`);
            }
            return;
        }
        if (this.thousand) {
            Object.assign(config, { thousand: this.thousand });
        }
        if (this.decimal) {
            Object.assign(config, { decimal: this.decimal });
        }
        if (this.expression) {
            Object.assign(config, { format: this.expression });
        }
        switch (firstLetter) {
            case 'C':
                Object.assign(config, { prefix: this.currencySymbol, precision });
                result = this.numberHelper.formatMoney(value, config);
                break;
            case 'D':
                result = this.toDecimal(value, precision);
                break;
            case 'F':
                Object.assign(config, { prefix: '', thousand: '', precision });
                result = this.numberHelper.formatMoney(value, config);
                break;
            case 'N':
                Object.assign(config, { prefix: '', precision });
                result = this.numberHelper.formatMoney(value, config);
                break;
            case 'P':
                result = this.toPercent(value, precision);
                break;
        }
        return result;
    }

    /**
     * @param value è½¬æ¢æåè¿å¶çæ°å­
     * @param precision åè¿å¶æ°å­çé¿åº¦
     */
    toDecimal(value: any, precision: any) {
        if (value.toString().indexOf('.') > -1) {
            if(this.debugSer) {
                this.debugSer.warn('åè¿å¶è½¬æ¢ä»éæ´åç±»å');
            }
            return;
        }
        return (Array(precision).join(this.filledSymbol) + value).slice(-precision);
    }
    /**
     * @param value è½¬æ¢æç¾åæ°çæ°å­
     * @param decimal å°æ°ç¹ä¿çå ä½
     */
    toPercent(value: any, precision: any) {
        return Number(value * 100).toFixed(Number(precision)) + '%';
    }

    /**
     * å°è´§å¸ç¬¦å·é¦å­æ¯è½¬æ¢æå¤§å
     */
    currencyToUpperCase(value: any) {
        return value.replace(/[a-z]+/g, (word: any) => {
            return word.toUpperCase();
        });
    }

    /**
     * å¤çå¹´ï¼Y=>y, D=>d
     */
    dateToUpperCase(value: any) {
        let newFormat: any;
        if (/Y/g.test(value) === true) {
            newFormat = value.replace(/Y/g, 'y');
        } else {
            newFormat = value;
        }
        if (/D/g.test(newFormat) === true) {
            newFormat = newFormat.replace(/D/g, 'd');
        }

        return newFormat;
    }
    /**
     * æä¸¾ç±»åå¤ç
     */
    getFormatEnumValue(value: any) {
        if (value == undefined || value === null || value === '') {
            return '';
        }
        if (this.data && this.data.length) {
            // å¯è½å­å¨å¤éæä¸¾ï¼ä½ä¸ºåéç¬¦  
            // let curVals = ('' + value).split(',');
            let curVals = ('' + value).split(this.enumDelimiter);
            let nameResult = [];
            for (let k = 0; k < curVals.length; k++) {
                let findObj = this.data.find(item => {
                    return item[this.valueField] == curVals[k];
                });
                if (findObj) {
                    nameResult.push(findObj[this.textField]);
                }
                else {
                    nameResult.push(curVals[k]);
                }
            }
            if (nameResult.length > 0) {
                // return nameResult.join(',');
                return nameResult.join(this.enumDelimiter);
            }
            return '';
        } else {
            return value;
        }
    }
    /**
     * booleanå¼å¤ç
     */
    getFormatCheckBoxValue(value: boolean) {
        if (value === true) {
            return this.localeService.getValue('text.yes');
        } else if (value === false || value == null || typeof value === 'undefined') {
            return this.localeService.getValue('text.no');
        } else {
            return value;
        }
    }


    private timeFormatTo(value: any, fmt: string, hourSystem = 12) {

    }

    private makeFormater(fmt: string): string {
        return fmt;
    }
    /*
    * æ¥ææ ¼å¼å
    */
    dateformat(value) {
        let str;
        if (!this.format) {
            this.format = 'YYYY-MM-DD';
        }

        if (this.format === 'timeago') {
            return this.dtHelper.relativeTime(value, this.localeService.localeId || 'zh-CHS');
        }

        // å½åå¼æ¯å·²ç»æ ¼å¼ååçæ¥æå­ç¬¦ä¸²
        if (this.returnFormat && this.returnFormat != 'yyyy-MM-dd' && typeof value == 'string') {
            value = this.getDateFromFormatedString(value, this.returnFormat);
        }
        //
        if (this.format === 'hh:mm:ss' || this.format === 'HH:mm:ss') {
            // æ ¼å¼åæ¶é´  ä¸´æ¶å¤ç
            str = value;
        } else {
            // if (this.showTime && value instanceof String) {
            //     str = value;
            // } else {
            //     const formArr = this.format.split(' ');
            //     if (this.showTime && formArr.length < 2) {
            //         formArr.push('HH:mm:ss');
            //         this.format = formArr.join(' ');
            //     }
            //     // æ ¼å¼åæ¥æ
            //     str = this.dtHelper.formatTo(value, this.dateToUpperCase(this.format));
            // }

            if (this.multiSelect && value) {
                const fmtList = ['yyyy-MM-dd', 'yyyy/MM/dd','MM/dd/yyyy','yyyyå¹´MMæddæ¥','yyyyMMdd'];
                if (!fmtList.find(n => n === this.format)) {
                    this.format = 'yyyy-MM-dd';
                }

                str = value.split(this.separator).map(n => {
                    if (n.indexOf('å¹´') > -1) {
                        n = n.replace('å¹´', '-');
                    }
                    if (n.indexOf('æ') > -1) {
                        n = n.replace('æ', '-');
                    }
                    if (n.indexOf('æ¥') > -1) {
                        n = n.replace('æ¥', '');
                    }
                    return this.dtHelper.formatTo(n, this.dateToUpperCase(this.format))
                }).join(this.separator);
            } else {
                str = this.dtHelper.formatTo(value, this.dateToUpperCase(this.format));
            }
        }
        return str;
    }
    private getDateFromFormatedString(dateString, format) {
        var result = { year: 0, month: 0, day: 0, hour: 0, minite: 0, second: 0 }; // å½ç¶è¿éå¯ä»¥é»è®¤1970-1-1æ¥
        if (dateString) {
            format.replace(/y+|Y+|M+|d+|D+|h+|H+|m+|s+/g, function (m, a, b, c) {// è¿éåªåäºå¹´ææ¥  å æ¶åç§ä¹æ¯å¯ä»¥ç
                dateString.substring(a).replace(/\d+/, function (d) { c = parseInt(d, 10) });
                if (/y+/i.test(m) && !result.year) result.year = c;
                if (/M+/.test(m) && !result.month) result.month = c;
                if (/d+/i.test(m) && !result.day) result.day = c;
                if (/h+/i.test(m) && !result.hour) result.hour = c;
                if (/m+/.test(m) && !result.minite) result.minite = c;
                if (/s+/.test(m) && !result.second) result.second = c;
            });
        }
        var resultDate = new Date(result.year + '/' + result.month + '/' + result.day + ' ' + result.hour + ':' + result.minite + ':' + result.second);
        return resultDate;
    }
    /**
     * æ ¼å¼åå¤ç
     */
    formatValue(value: any) {
        let str: any;
        if (this.currentLanguage) {
            return this.formatLang(value);
        }
        // if (!value) {
        //     return '';
        // }
        if (this.type !== 'boolean' && (value == null || typeof value === 'undefined')) {
            return '';
        }
        switch (this.type) {
            case 'string':
                str = value;
                break;
            case 'date':
            case 'datetime':
                if (!this.dateRange) {
                    // if (!this.format) {
                    //     this.format = 'YYYY-MM-DD';
                    // }
                    // if (this.format === 'hh:mm:ss') {
                    //     // æ ¼å¼åæ¶é´  ä¸´æ¶å¤ç
                    //     str = value;
                    // } else {
                    //     if (this.showTime) {
                    //         str = value;
                    //     } else {
                    //         // æ ¼å¼åæ¥æ
                    //         str = this.dtHelper.formatTo(value, this.dateToUpperCase(this.format));
                    //     }
                    // }

                    str = this.dateformat(value);
                } else {
                    const dateValues = value.split(this.dateRangeDelimiter);

                    let dataStr = [];
                    if (dateValues && dateValues.length) {
                        dateValues.forEach(date => {
                            let val = this.dateformat(date);
                            dataStr.push(val);
                        });
                    }
                    str = dataStr.join(this.dateRangeDelimiter);
                }

                break;
            // case 'dateTime':
            //     str = this.timeFormatTo(value, this.makeFormater(this.format));
            //     break;
            case 'number':
                str = this.getFormatNumberValue(value);
                break;
            case 'enum':
                str = this.getFormatEnumValue(value);
                break;
            case 'boolean':
                str = this.getFormatCheckBoxValue(value);
                break;
            default:
                break; // throw new Error(`æä¸æ¯æ ${this.type} ç±»å`);
        }

        return str;
    }

    private setText2Star(value) {
        if (this.isPassword) {
            const star = value ? '******' : '';
            this.text = star;
            this.control = star;

            return true;
        }
        return false;
    }

    writeValue(value: any): void {
        // lucas 2021-08-27
        if (this.setText2Star(value)) {
            return;
        }

        // xia 2021-03-13
        if (this.beforeWriteValue) {
            const options = {
                localizationType: this.localizationType,
                showTime: this.showTime,
                ref: this
            };
            const text = this.beforeWriteValue(value, options);
            if (text !== undefined) {
                this.text = text;
                return;
            }
        }
        this.control = this.formatValue(value);
        this.controlChange(value);
    }

    registerOnChange(fn: () => {}): void {
        this.controlChange = fn;
    }

    registerOnTouched(fn): void {
        this.controlTouch = fn;
    }

}
